@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
}
body {
  font-family: "Noto Sans KR", sans-serif;
}


.header__container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-bottom: 1px solid #f9f9f9;
  object-fit: contain;
  z-index: 100;
}
.header__logo {
  width: 40px;
}

.swipeButtons_container {
  position: fixed;
  bottom: 7vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.swipeButtons_container .MuiIconButton-root {
  background-color: white;
  box-shadow: 0 18px 53px 0 rgba(0, 0, 0, 0.3);
}
.swipeButton__repeat {
  color: #f5b748 !important;
}
.swipeButton__close {
  color: #ec5e6f !important;
}
.swipeButton__star {
  color: #62b4f9 !important;
}
.swipeButton__favorite {
  color: #76e2b3 !important;
}
.swipeButton__flash {
  color: #915dd1 !important;
}

.cards__container {
  display: flex;
  justify-content: center;
  margin-top: 10vh;
}
.card {
  position: relative;
  height: 60vh;
  width: 40vw;
  max-width: 85vw;
  box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
  background-size: cover;
  background-position: center;
  border-radius: 10px;
}
.card h3 {
  position: absolute;
  bottom: 0;
  padding: 5px;
  color: aliceblue;
}
.swipe {
  position: absolute;
  cursor: pointer;
}

